
.loading {
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}
.loading .img {
  margin: calc(50vh - 15vw) auto;
  width: 30vw;
  height: 30vw;
  border-radius: 10px;
  background: url("../assets/image/loading.gif") no-repeat;
  background-size: cover;
}


